<template>
  <el-button type="info" class="p-search" @click="timetableExcel()">excel</el-button>
</template>

<script>
import FileSaver from "file-saver";
import XLSX from "xlsx";
export default {
  components: {},
  props: {
    value: {
      type: String,
      require: true
    },
    tvalue: {
      type: String,
      require: true
    },
    tableName: {
      type: String,
      require: true
    }
  },
  data() {
    return {};
  },
  watch: {},
  computed: {},
  methods: {
    //
    timetableExcel() {
      if (this.value != null && this.tvalue != null) {
        if (!isNaN(this.value)) {
          var newValue = "" + this.value + "";
        } else {
          var newValue = this.value;
        }
      }
      this.$confirm(
        "" + this.tvalue + newValue + "",
        "excel",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "success"
        }
      )
        .then(() => {
          this.exportToExcel(this.tvalue, newValue); //
          this.$message({
            type: "success",
            message: "!"
          });
          this.value = "";
          this.tvalue = "";
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    // excel
    // value
    // newValue
    exportToExcel(value, newValue) {
      let et = XLSX.utils.table_to_book(
        document.getElementById(this.tableName)
      ); //tableDOM
      let etout = XLSX.write(et, {
        bookType: "xlsx",
        bookSST: true,
        type: "array"
      });
      try {
        FileSaver.saveAs(
          new Blob([etout], {
            type: "application/octet-stream"
          }),
          value + newValue + ".xlsx"
        ); //trade-publish.xlsx 
      } catch (e) {
        console.log(e, etout);
      }
      return etout;
    }
  },
  created() {},
  mounted() {}
};
</script>
<style scoped>
.p-search {
  float: right;
}
</style>


